<template>
  <div class="user-list">
    <div class="border">
      <div class="user-list-title">
          用户
      </div>
      <div class="user-enum">
        <transition-group tag="ul" name="userfade" mode="out-in">
          <li v-for="(item, index) in user" :key="index">

          </li>
        </transition-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data:  function (params) {
    return {
      user: []
    }
  }
}
</script>

<style>
  .user-list {
    height: 600px;
    padding: 10px 30px;
    width: 300px;
  }
  .border {
    height: 600px;
    border: 1px solid #cccccc;
    border-radius: 14px;
    padding: 5px 5px;
    overflow: hidden;
  }
  .user-list-title {
    height: 30px;
    background: #CCCCCC;
    color: #ffffff;
    line-height: 30px;
    padding-left: 25px;
    font-size: 18px;
    border-radius: 14px;
  }
</style>



