<template>
  <div class="message">
    <message-list message="" class="left"></message-list>
    <user-list class="right"></user-list>
  </div>
</template>

<script>
  import messageList from './message-list.vue';
  import userList from './user-list.vue';

  export default {
    components: {
      "message-list": messageList,
      "user-list": userList,
    },
    data: function () {
      return {
        msgList: [],
        usgList: [],
      }
    },
    created: function () {
      let ws = new WebSocket('ws://127.0.0.1:3000/ws/chat');

      ws.onmessage = function(event) {
          let data = event.data;
          console.log(data);
          let msg = JSON.parse(data);
          if (msg.type === 'list') {
              vmUserList.users = msg.data;
          } else if (msg.type === 'join') {
              addToUserList(vmUserList.users, msg.user);
              addMessage(vmMessageList.messages, msg);
          } else if (msg.type === 'left') {
              removeFromUserList(vmUserList.users, msg.user);
              addMessage(vmMessageList.messages, msg);
          } else if (msg.type === 'chat') {
              addMessage(vmMessageList.messages, msg);
          }
      };
    },
    methods: {

    }
  }
</script>

<style>
  .message {
    margin: 10px 2px;
    min-height: 620px;
    border: 1px solid black;
    position: relative;
  }
  .left {
    position: absolute;
    left: 0px;
  }
  .right {
    position: absolute;
    right: 0px;
  }
</style>



